<template>
	<div>
		<ul class="g_formList">
			
			<!--<li class="formList_li"><span class="character"><b>*</b> 报考工种</span><input class="text" type="text" placeholder="请输入报考工种" v-model="title"/></li>-->
			<li class="formList_li">
				<span class="character"><b>*</b> 报考工种</span>
				<select class="text" name=""  v-model="info.title">
					<option v-for="item in listtitle" v-bind:value="item.value">{{item.text}}</option>
				</select>
			</li>
			<li class="formList_li">
				<span class="character"><b>*</b> 报考级别</span>
				<select class="text" name=""  v-model="info.level">
					<option v-for="item in listrank" v-bind:value="item.value">{{item.text}}</option>
				</select>
			</li>
			
			<li class="formList_li">
				<span class="character"><b>*</b> 报考类型</span>
				<select class="text" name="" v-model="info.type">
					<option v-for="item in listtype" v-bind:value="item.value">{{item.text}}</option>
				</select>
			</li>
			
			<li class="formList_li">
				<span class="character"><b>*</b> 报考科目</span>
				<div class="radio_div" style="width: 80%;">
					<div class="radio_size">
						<input type="radio" name="subject" id="" value="1" v-model="info.subject"/>理论
					</div>
					<div class="radio_size">
						<input type="radio" name="subject" id="" value="2" v-model="info.subject"/>技能
					</div>
				</div>
			</li>
			<li class="formList_li">
				<div class="radio_div">
					<div class="radio_size">
						<input type="radio" name="subject" id="" value="3" v-model="info.subject"/>论文辩论
					</div>
					<div class="radio_size">
						<input type="radio" name="subject" id="" value="4" v-model="info.subject"/>综合评审
					</div>
				</div>
			</li>
			
			<li class="formList_li">
				<span class="character"><b>*</b> 现技术等级或职称等级</span>
				<select class="texts" name="" v-model="info.skillLevel">
					<option v-for="item in listskillLevel" v-bind:value="item.value">{{item.text}}</option>
				</select>
			</li>
			<li class="formList_li">
				<select class="texts" name="" v-model="info.positionLevel">
					<option v-for="item in listpositionLevel" v-bind:value="item.value">{{item.text}}</option>
				</select>
			</li>
			
			<li class="formList_li" style="height: 1.4rem;"><span class="character"><b>&nbsp;</b> 考生来源</span><span class=""><b>&nbsp;</b> 其他（默认）</span></li>
			<li class="formList_li">
				<span class="character"><b>*</b> 考生学历</span>
				<select class="text" name="" v-model="info.education">
					<option v-for="item in listeducation" v-bind:value="item.value">{{item.text}}</option>
				</select>
			</li>
			
			<li class="hint"><p>应满足工种报考要求和工作年限要求。例如：一级劳协要 求初中以上学历，且工作满19年，所提供学历则需初中或 以上学历且毕业已满19年</p></li>
			
			<li class="formList_li"><span class="character"><b>*</b> 报考学历的上学时间</span></li>
			
			<div class="timedata">
				<div class="block">
					<span class="demonstration">起</span>
					<el-date-picker
						value-format="yyyy-MM"
					    v-model="info.educationStartTime"
					    type="month"
					    align="left"
					    placeholder="选择月">
					</el-date-picker>
				</div>
				<div class="block">
					<span class="demonstration">止</span>
					<el-date-picker
						value-format="yyyy-MM"
					    v-model="info.educationEndTime"
					    type="month"
					    align="right"
					    placeholder="选择月">
					</el-date-picker>
				</div>
			</div>
			
			<li class="formList_li"><span class="character"><b>*</b> 报考学校的名称</span></li>
			<li class="formList_li_B">
				<input class="text" type="text" placeholder="请填写学校名称" v-model="info.educationSchoolName">
			</li>
			<li class="hint"><p>所填名称应与学历证学校盖章一致</p></li>
			<li class="formList_li"><span class="character"><b>*</b> 专业名称</span><input class="text" type="text" placeholder="请填写专业名称" v-model="info.major"></li>
			<li class="hint"><p>高技、中专或大专需要写上专业名称</p></li>
			
			<li class="formList_li">
				<span class="character"><b>*</b> 学位</span>
				<select class="text" name="" v-model="info.degree">
					<option v-for="item in listdegree" v-bind:value="item.value">{{item.text}}</option>
				</select>
			</li>
			
			<li class="formList_li">
				<span class="character"><b>*</b> 最高学历</span>
				<select class="text" name="" v-model="info.hightestEducation">
					<option v-for="item in listhightestEducation" v-bind:value="item.value">{{item.text}}</option>
				</select>
			</li>
			
			<li class="formList_li"><span class="character"><b>*</b> 最高学历的上学时间</span></li>
			
			<div class="timedata">
				<div class="block">
					<span class="demonstration">起</span>
					<el-date-picker
						value-format="yyyy-MM"
					    v-model="info.hightestEducationStartTime"
					    type="month"
					    align="left"
					    placeholder="选择月">
					</el-date-picker>
				</div>
				<div class="block">
					<span class="demonstration">止</span>
					<el-date-picker
						value-format="yyyy-MM"
					    v-model="info.hightestEducationEndTime"
					    type="month"
					    align="right"
					    placeholder="选择月">
					</el-date-picker>
				</div>
			</div>
			
			<li class="formList_li"><span class="character"><b>*</b> 最高学历的学校名称</span></li>
			<li class="formList_li_B">
				<input class="text" type="text" placeholder="请填写学校名称" v-model="info.hightestEducationSchoolName">
			</li>
			<li class="hint"><p>所填名称应与学历证学校盖章一致</p></li>
			
			<li class="formList_li"><span class="character"><b>&nbsp;</b> 专业名称</span><input class="text" type="text" placeholder="请填写专业名称" v-model="info.hightestEducationMajor"></li>
			<li class="hint"><p>高技、中专或大专需要写上专业名称</p></li>
			
			<li class="formList_li">
				<span class="character"><b>&nbsp;</b> 学位</span>
				<select class="text" name="" v-model="info.hightestEducationDegree">
					<option v-for="item in listhightestEducationDegree" v-bind:value="item.value">{{item.text}}</option>
				</select>
			</li>
		</ul>
		
		<div v-if="btns" class="button_lack" >保存并返回</div>
		<div v-if="!btns" class="button_whole" @click="UserExamInfoSubmit()">保存并返回</div>
		
		
	</div>
</template>

<script>
	
	export default{
		
		data(){
			return {
				btns:true,
				message:{},
				listtitle:[
					{ text: '请选择', value: 0 },
				    { text: '人力资源', value: 1 },
				    { text: '劳动协调', value: 2 }
				],
				listrank:[
				 { text: '请选择', value: 0 },
			      { text: 1, value: 1 },
			      { text: 2, value: 2 },
			      { text: 3, value: 3 },
			      { text: 4, value: 4 },
			      { text: 5, value: 5 }
				],
				listtype:[
					{ text: '请选择', value: 0 },
				    { text: '新考', value: 1 },
				    { text: '补考', value: 2 }
				],
				listskillLevel:[
				 { text: '无等级', value: 0 },
			      { text: '一级', value: 1 },
			      { text: '二级', value: 2 },
			      { text: '三级', value: 3 },
			      { text: '四级', value: 4 },
			      { text: '五级', value: 5 }
				],
				listpositionLevel:[
				 { text: '无职称', value: 0 },
			      { text: '初级', value: 1 },
			      { text: '中级', value: 2 },
			      { text: '高级', value: 3 },
				],
				listeducation:[
				  { text: '请选择', value: 0 },
				  { text: '博士', value: 1 },
			      { text: '硕士', value: 2 },
			      { text: '本科', value: 3 },
			      { text: '大专', value: 4 },
			      { text: '高技', value: 5 },
			      { text: '技校', value: 6 },
			      { text: '中专', value: 7 },
			      { text: '高中', value: 8 },
			      { text: '初中', value: 9 }
				],
				listdegree:[
				  { text: '无', value: 0 },
			      { text: '博士', value: 1 },
			      { text: '硕士', value: 2 },
			      { text: '学士', value: 3 },
				],
				listhightestEducation:[
				  { text: '请选择', value: 0 },
				  { text: '博士', value: 1 },
			      { text: '硕士', value: 2 },
			      { text: '本科', value: 3 },
			      { text: '大专', value: 4 },
			      { text: '高技', value: 5 },
			      { text: '技校', value: 6 },
			      { text: '中专', value: 7 },
			      { text: '高中', value: 8 },
			      { text: '初中', value: 9 }
				],
				listhightestEducationDegree:[
				  { text: '无', value: 0 },
			      { text: '博士', value: 1 },
			      { text: '硕士', value: 2 },
			      { text: '学士', value: 3 },
				],
				info:{
	        		'title': 0,//报考工种  1人力资源  2劳动协调
		         	'level': 0,//报考级别  1--5级
		         	'type': 0, //报考类型  1新考  2补考
		         	'subject': 0,//报考科目  1理论  2技能 3论文答辩 4综合
		         	'skillLevel': 0,//技术等级  0无等级  1--5级
		         	'positionLevel': 0,//职称等级  0无等级  1初 2中 3高
		         	'source': 0,//考生来源 
		         	'education': 0,//报考学历 1--9
		         	'educationStartTime': "",//报考学历开始时间  
		         	'educationEndTime': "",//报考学历截止时间
		         	'educationSchoolName': "",//报考学历学校名称
		         	'hightestEducation': 0,//最高学历 1--9
		         	'hightestEducationStartTime': "",//最高学历开始时间  
		         	'hightestEducationEndTime': "",//最高学历截止时间 
		         	'hightestEducationSchoolName': "",//最高学历学校名称
		         	'major': "",//专业名称
		         	'degree': 0,//学位
		         	'hightestEducationMajor': "",//最高学历专业名称
		         	'hightestEducationDegree': 0,//最高学历学位
	        	}
			}
		},
		methods:{
			UserExamInfo(){
				this.$Api.UserExamInfo(this.$store.state.sessionId).then((res)=>{
					this.message = res.q.info;
					this.info.title = this.message.title;
					this.info.level = this.message.level;						//报考级别
					this.info.type = this.message.type;						//报考类型
					this.info.subject = this.message.subject;							//报考科目
					this.info.skillLevel = this.message.skillLevel;			//现技术等级
					this.info.positionLevel = this.message.positionLevel;	//现职称等级
					if(this.message.source!=null){
						this.info.source = this.message.source;//考生来源
					}else{
						this.info.source = 0
					}
					
					this.info.education = this.message.education;		//报考学历	
					
					if(this.message.educationStartTime!=null){
						this.info.educationStartTime = this.message.educationStartTime						//报考学历开始时间
					}
					if(this.message.educationEndTime!=null){
						this.info.educationEndTime = this.message.educationEndTime							//报考学历截止时间
					}
					this.info.educationSchoolName = this.message.educationSchoolName	//报考学历学校名称
					this.info.degree = this.message.degree;					//报考学位	
					this.info.major = this.message.major;	//报考专业
					this.info.hightestEducation = this.message.hightestEducation;		//最高学历	
					this.info.hightestEducationSchoolName = this.message.hightestEducationSchoolName//最高学历学校名字
					this.info.hightestEducationMajor = this.message.hightestEducationMajor//最高学历专业名字
					this.info.hightestEducationDegree = this.message.hightestEducationDegree;	//最高学历学位
					if(this.message.hightestEducationStartTime!=null){
						this.info.hightestEducationStartTime = this.message.hightestEducationStartTime							//最高学历上学时间
					}
					if(this.message.hightestEducationEndTime!=null){
						this.info.hightestEducationEndTime = this.message.hightestEducationEndTime								//最高学历截止时间
					}
			  	})
			},
			UserExamInfoSubmit(){
				
				this.info={
	        		'title': this.info.title,//报考工种  1人力资源  2劳动协调
		         	'level': this.info.level,//报考级别  1--5级
		         	'type': this.info.type, //报考类型  1新考  2补考
		         	'subject': this.info.subject,//报考科目  1理论  2技能 3论文答辩 4综合
		         	'skillLevel': this.info.skillLevel,//技术等级  0无等级  1--5级
		         	'positionLevel': this.info.positionLevel,//职称等级  0无等级  1初 2中 3高
		         	'source': this.info.source,//考生来源 
		         	'education': this.info.education,//报考学历 1--9
		         	'educationStartTime': this.info.educationStartTime,//报考学历开始时间  
		         	'educationEndTime': this.info.educationEndTime,//报考学历截止时间
		         	'educationSchoolName': this.info.educationSchoolName,//报考学历学校名称
		         	'hightestEducation': this.info.hightestEducation,//最高学历 1--9
		         	'hightestEducationStartTime': this.info.hightestEducationStartTime,//最高学历开始时间  
		         	'hightestEducationEndTime': this.info.hightestEducationEndTime,//最高学历截止时间 
		         	'hightestEducationSchoolName': this.info.hightestEducationSchoolName,//最高学历学校名称
		         	'major': this.info.major,//专业名称
		         	'degree': this.info.degree,//学位
		         	'hightestEducationMajor': this.info.hightestEducationMajor,//最高学历专业名称
		         	'hightestEducationDegree': this.info.hightestEducationDegree,//最高学历学位
	        	}
				
				this.$Api.UserExamInfoSubmit(this.$store.state.sessionId,this.info).then((res)=>{
					this.$toast.center('保存成功');
					 setTimeout(() => { 
			            this.$router.go(-1);
			        }, 1000)
				})
			},
			getAnswer(){
				console.log(this.info)
				if(this.info.title!=null&&this.info.title!=""&&this.info.level!=null&&this.info.level!=""&&this.info.type!=null&&this.info.type!=""&&this.info.subject!=null&&this.info.subject!=""&&this.info.skillLevel!=null&&this.info.skillLevel!=""&&this.info.positionLevel!=null&&this.info.positionLevel!=""&&this.info.source!=null&&this.info.source!=""&&this.info.educationStartTime!=null&&this.info.educationStartTime!=""&&this.info.educationEndTime!=null&&this.info.educationEndTime!=""&&this.info.educationSchoolName!=null&&this.info.educationSchoolName!=""&&this.info.degree!=null&&this.info.degree!=""&&this.info.major!=null&&this.info.major!=""&&this.info.hightestEducation!=null&&this.info.hightestEducation!=""&&this.info.hightestEducationSchoolName!=null&&this.info.hightestEducationSchoolName!=""&&this.info.hightestEducationStartTime!=null&&this.info.hightestEducationStartTime!=""&&this.info.hightestEducationEndTime!=null&&this.info.hightestEducationEndTime!=""){
					this.btns = false;
				}else{
					this.btns = true;
				}
			},
		},
		mounted(){
	        this.UserExamInfo()
	    },
	    watch:{
			info:{
				handler(val, oldVal){
				this.getAnswer()
				},
				 deep:true
			}
        },
	}
</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	
	input[type=text], input[type=password], input[type=submit], select { border: #cccccc 1px solid; height: .8rem; border-radius: .1rem; width: 100%; }
	input[type=text]:focus, input[type=password]:focus, input[type=submit]:focus, select:focus { border-color: #fa8a12; }
	input[type=text], input[type=password] { padding: 0 .1rem; }
	input[type=submit] { background-color: #fa8a12; text-align: center; width: 100%; }
	textarea { height: auto; min-height: 1.2rem; border: #cccccc 1px solid; border-radius: .1rem; padding: .2rem .3rem; width: 100%; }
	input:-moz-placeholder { color: #999999; }
	::-webkit-input-placeholder { color: #999999; }
	input[type=text], input[type=password] { -webkit-appearance: none; }
	
	.g_formList{padding: .3rem 0.3rem 0;
		.formList_li{padding-left: 2.3rem; position: relative; height: .7rem; margin-bottom: .3rem; font-size: .3rem; line-height: .7rem;
			.character{position: absolute; left: 0;color: #1a1a1a;font-weight: 600;
				b{color: #f32929;}
			}
			.text{height: .7rem;padding:0 0.1rem;width: 4rem;float: right;margin-right: 0.55rem;}
			.texts{height: .7rem;padding:0 0.1rem;width: 3rem;float: right;margin-right: 0.55rem;}
			.radio_div{display: flex;flex-wrap: wrap;
				.radio_size{width: 50%;}
			}
		}
		.hint{margin-bottom: .3rem;
			p{font-size: .25rem;color: #fa8a12;padding:0 0.4rem 0 0.18rem;}
		}
		.formList_li_B{font-size: .3rem;height: .7rem;line-height: .7rem;margin-bottom: .3rem;
			.text{width: 90%;margin-left: 0.18rem;}
			.formList_li_div{display:inline-block;
				.character{color: #1a1a1a;font-weight: 600;}
				.text_c{width: 2.6rem;}
			}
			.div_padding{padding-left:0.2rem;}
		}
		.timedata{
			display: flex;width: 100%;height: 0.8rem;
			.block{
				width: 50%;display: flex;height: 0.8rem;
				.demonstration{font-size: 0.3rem;height: 0.8rem;line-height: 0.8rem;padding:0 0.2rem;}
			}
		}
	}
	
	
	
	.button_lack{width: 6.9rem;height: 1rem;background-color: #ccc;font-size: 0.34rem;color: #FFFFFF;text-align: center;line-height: 1rem;border-radius:0.5rem ;margin:0.5rem auto 1.4rem;}
	.button_whole{width: 6.9rem;height: 1rem;background-color: #fa8a12;font-size: 0.34rem;color: #FFFFFF;text-align: center;line-height: 1rem;border-radius:0.5rem ;margin:0.5rem auto 1.4rem;}
</style>


